<template>
    <div class="menu-buttons">
        <a href="#about">
            <div :class="get_actual_cl_1">
                <v-icon class="menu-button-icon" name="info"/>
                <div class="button-name">o mnie</div>
            </div>
        </a>
        <a href="#portfolio">
            <div :class="get_actual_cl_2">
                <v-icon class="menu-button-icon" name="airplay"/>
                <div class="button-name">projekty</div>
            </div>
        </a>
        <a href="#contact">
            <div :class="get_actual_cl_3">
                <v-icon class="menu-button-icon" name="smartphone"/>
                <div class="button-name">kontakt</div>
            </div>
        </a>
    </div>
</template>

<script>
    export default {
        name: "MenuButtons",
        data: function () {
            return {
                actual_class_1: "button",
                actual_class_2: "button",
                actual_class_3: "button"
            }
        },
        mounted() {
            window.addEventListener('scroll', this.set_cl);
            this.set_cl();
        },
        computed: {
            get_actual_cl_1() {
                return this.actual_class_1;
            },
            get_actual_cl_2() {
                return this.actual_class_2;
            },
            get_actual_cl_3() {
                return this.actual_class_3;
            }
        },
        methods: {
            set_cl() {
                this.actual_class_1 = "button";
                this.actual_class_2 = "button";
                this.actual_class_3 = "button";
                const about = document.getElementById('about').offsetHeight;
                const header = document.getElementById('header').offsetHeight;
                const portfolio = document.getElementById('portfolio').offsetHeight;
                const contact = document.getElementById('contact').offsetHeight;

                if (window.scrollY <= about + header) {
                    this.actual_class_1 = "button chosen";
                } else if (window.scrollY > about + header && window.scrollY <= about + header + portfolio) {
                    this.actual_class_2 = "button chosen";
                } else if (window.scrollY > about + header + portfolio &&
                    window.scrollY <= about + header + portfolio + contact) {
                    this.actual_class_3 = "button chosen";
                }

            }
        }
    }
</script>

<style scoped>
    a {
        text-decoration: none;
    }

    a:link {
        color: white;
    }

    a:visited {
        color: white;
    }

    .menu-buttons {
        width: 100%;
    }

    .menu-button-icon {
        width: 50px;
        height: 100%;
        margin: 0 10px;
    }

    .button {
        width: 100%;
        font-size: 22px;
        cursor: pointer;
        height: 60px;
        border-bottom: 2px solid white;
        display: flex;
        background-color: #34495E;
        justify-content: flex-start;
        font-weight: bold;
        align-items: center;
    }

    .chosen {
        background-color: #7F8C8D;
    }

    .chosen:hover {
        color: white !important;
    }

    .button:hover {
        color: #CCD1D1;
        background-color: #7F8C8D;
    }
</style>