<template>
    <div class="menu">
        <div class="name-surname">
            <div>Mikołaj Sojka</div>
            <div class="front">Front-end develeper</div>
        </div>

        <div :class="get_clicked? `menu-icon clicked ${get_menu_icon_color}`: `menu-icon ${get_menu_icon_color}`"
             @click="sidebar_clicked">
            <v-icon class="icon-menu" name="menu"/>
        </div>
        <div class="position">
            <div v-if="get_responsive_pc" class="route">
                <a href="#about"><span class="menu-item chosen">O mnie</span></a>
                <Separator/>
            </div>
            <div v-if="get_responsive_pc" class="route">
                <a href="#portfolio"><span class="menu-item">Projekty</span></a>
                <Separator/>
            </div>
            <div v-if="get_responsive_pc" class="route">
                <a href="#contact"><span class="menu-item">Kontakt</span></a>
            </div>
        </div>
        <div v-if="get_responsive_pc && get_responsive_mobile" class="route"><span>Mikołaj Sojka</span></div>

    </div>
</template>

<script>
    import Separator from "../atoms/Separator";

    export default {
        name: "Menu",
        components: {
            Separator
        },
        data: function () {
            return {
                responsive_pc: true,
                responsive_mobile: true,
                clicked: false,
                menu_icon_color: 'default'
            };
        },
        mounted() {
            window.addEventListener('resize', this.resize);
            window.addEventListener('scroll', this.on_scroll);
            this.resize();
        },
        computed: {
            get_clicked() {
                return this.clicked;
            },
            get_responsive_pc() {
                return this.responsive_pc
            },
            get_responsive_mobile() {
                return this.responsive_mobile
            },
            get_menu_icon_color() {
                return this.menu_icon_color;
            }
        },
        methods: {
            on_scroll() {
                const header = document.getElementById('header').offsetHeight
                this.menu_icon_color = 'default';
                if (window.scrollY > header / 2) {
                    this.menu_icon_color = 'changed';
                }
            },
            sidebar_clicked(e) {
                this.clicked = !this.clicked;
                this.$parent.$emit('sidebar_clicked', e)
            }
            ,
            resize() {
                if (window.innerWidth < 700) {
                    this.responsive_pc = false;
                    this.responsive_mobile = true;
                } else {
                    this.responsive_pc = true;
                    this.responsive_mobile = false;
                }
            }
        }
    }
</script>

<style scoped>
    .name-surname {
        font-family: "Courier New", Courier, monospace;
        color: white;
        position: absolute;
        left: 20px;
        font-size: 20px;
        border-left: 3px solid #566573;
        padding-left: 5px;
        top: 5px;
        display: flex;
        flex-direction: column;
    }

    .front {
        font-size: 14px;
    }

    .default {
        background-color: #5DADE2;
    }

    .changed {
        background-color: #34495E;
    }

    a {
        text-decoration: none;
    }

    a:link {
        color: white;
    }

    a:visited {
        color: white;
    }

    .menu {
        height: 70px;
        display: flex;
        font-weight: bold;
    }

    @media only screen and (max-width: 600px) {
        .name-surname {
            left: auto !important;
            right: 20px;
            border-left: none;
            border-right: 3px solid #566573;
            padding-left: 0px;
            padding-right: 5px;
        }

        .menu {
            flex-direction: row-reverse;
        }

        .route {
            position: absolute;
            left: 120px;
        }
    }

    .position {
        position: absolute;
        width: auto;
        display: flex;
        left: 90px;
    }

    .route {
        height: 70px;
        color: white;
        font-size: 26px;
        padding-left: 20px;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .menu-item:hover {
        color: #CCD1D1;
    }

    .menu-icon {
        width: 60px;
        color: white;
        cursor: pointer;
        position: fixed;
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    }

    .menu-item {
        cursor: pointer;
    }

    .clicked {
        color: #CCD1D1;
    }

    .chosen {
        color: #CCD1D1;
    }

    .icon-menu:hover {
        color: #CCD1D1;
    }
</style>