<template>
    <div v-show="is_visible" class="side-bar">
        <MenuButtons/>
    </div>
</template>

<script>
    import MenuButtons from "../molecules/MenuButtons";

    export default {
        name: "SideBar",
        props: {
            clicked_menu: {type: Boolean, default: false}
        },
        components: {MenuButtons},
        computed: {
            is_visible() {
                return this.clicked_menu;
            }
        }
    }
</script>

<style scoped>
    @media only screen and (max-width: 600px) {
        .side-bar {
            right: 20px;
            left: auto !important;
        }
    }

    .side-bar {
        width: 180px;
        height: auto;
        position: fixed;
        background-color: #839192;
        color: white;
        top: 202px;
        left: 20px;
    }
</style>