<template>
    <div class="header" id="header">
        <Menu/>
        <ProfilePhoto/>
    </div>
</template>

<script>
    import ProfilePhoto from "../atoms/ProfilePhoto";
    import Menu from "../molecules/Menu";

    export default {
        name: "Header",
        components: {ProfilePhoto, Menu},
        mounted() {
            this.$on("sidebar_clicked", this.sidebar_clicked);
        },
        methods: {
            sidebar_clicked() {
                this.$parent.$emit("sidebar_clicked");
            }
        }
    }
</script>

<style>
    @media only screen and (max-width: 600px) {
        .header {
            flex-direction: row-reverse;
        }
    }

    .header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0 20px;
        min-height: 200px;
        background-color: #5DADE2;
        margin: 0;
        border-bottom: 2px solid white;
    }
</style>